import React from 'react'
import styled from 'styled-components'
import { useEffect } from 'react'
import { useRef } from 'react'
import { useState } from 'react'



export default function NumberCounter({title, icon, number, maxNr, setNumber}) {
    const numberRef = useRef()
    const [top, setTop] = useState(true)

    function whenScrolled() {
        if (number < maxNr) {
            let increment = maxNr / 50
            setNumber(Math.ceil(number += increment))
            setTimeout(whenScrolled, 10)
        }
    }
    
    useEffect(() => {

        
        
        // if (top) {
            const updateCounters = () => {
                console.log(top)
                if (window.pageYOffset < 500) {
                    setTop(true)
                }
                if (top && numberRef.current.getBoundingClientRect().y < window.innerHeight) {
                    whenScrolled()
                    setTop(false)
                }
                // console.log(top, window.scrollY)
            }
            window.addEventListener('scroll', updateCounters)
    
            return () => window.removeEventListener('scroll', updateCounters)
        // }

    }, [])

    return (
        <Wrapper ref={numberRef}>
            {icon}
            <Numbers>{(number).toLocaleString('ro')}+</Numbers>
            <Span>{title}</Span>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    flex: 1 1 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin: 30px 50px;
    >svg {
        font-size: 2rem;
    }
`
const Numbers = styled.p`
    font-size: clamp(1.6rem, 5vw, 3rem);
    margin-top: 10px;
`
const Span = styled.span`

`